import react, { Component } from 'react';
import "../static/style/main.css";
import { Layout, Menu, Breadcrumb, Table, Button, Space, message, Popconfirm } from 'antd';
import { _getDataAll, _deleteImg } from "../core/server";

const { Header, Content, Footer } = Layout;
const { Column, ColumnGroup } = Table;

class index extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: []
        };
    }

    async getDataAll() {
        const res = await _getDataAll()
        console.log(res)
        if (res.data.total > 0) {
            this.setState({
                data: res.data.data
            })
        }else if(res.data.total === 0){
            this.setState({
                data: []
            })
        }
    }

    async deleteImg(data) {
        const res = await _deleteImg(data)
        console.log(res)
        if (res.status === 200) {
            message.success('删除成功!');
            this.getDataAll();
        } else {
            message.warning('删除失败!');
        }
    }

    confirm = (id, e) => {
        console.log(id);
        this.deleteImg({ "img_id": id })
        // message.success('Click on Yes');
    }

    cancel = (e) => {
        // console.log(e);
        message.error('Click on No');
    }

    componentDidMount() {
        this.getDataAll()
    }
    render() {
        return (
            <div>
                <Layout className="layout">
                    <Header>
                        <div className="logo" />
                        <Menu theme="dark" mode="horizontal" defaultSelectedKeys={['2']}>
                            <Menu.Item key="1">首页</Menu.Item>
                            <Menu.Item key="2">详细数据</Menu.Item>
                            <Menu.Item key="3">nav 3</Menu.Item>
                        </Menu>
                    </Header>
                    <Content style={{ padding: '0 50px' }}>
                        <Breadcrumb style={{ margin: '16px 0' }}>
                            {/* <Breadcrumb.Item>Home</Breadcrumb.Item>
                            <Breadcrumb.Item>List</Breadcrumb.Item>
                            <Breadcrumb.Item>App</Breadcrumb.Item> */}
                        </Breadcrumb>
                        <div className="site-layout-content">

                            <Table dataSource={this.state.data}>

                                <Column title="ID" dataIndex="ID" key="ID" />
                                <Column title="图片ID" dataIndex="IMGID" key="IMGID" />
                                <Column title="文件保存路径" dataIndex="FilePath" key="FilePath" />
                                <Column title="原始文件名" dataIndex="OldName" key="OldName" />
                                <Column title="访问次数" dataIndex="ViewNum" key="ViewNum" />
                                <Column title="上传时间" dataIndex="AddTime" key="AddTime" />
                                <Column title="上传IP" dataIndex="AddIP" key="AddIP" />
                                <Column title="上传UA" dataIndex="AddUA" key="AddUA" />
                                <Column
                                    title="Action"
                                    key="action"
                                    render={(text, record) => (
                                        <Space size="middle">
                                            {/* <a>Invite {record.lastName}</a> */}
                                            <Popconfirm
                                                title="确定删除该文件吗?"
                                                onConfirm={this.confirm.bind(this, record.IMGID)}
                                                onCancel={this.cancel}
                                                okText="Yes"
                                                cancelText="No"
                                            >
                                                <Button danger>
                                                    删除
                                                </Button>
                                            </Popconfirm>


                                        </Space>
                                    )}
                                />
                            </Table>

                        </div>
                    </Content>
                    <Footer style={{ textAlign: 'center' }}>Ant Design ©2018 Created by Ant UED</Footer>
                </Layout>
            </div>
        );
    }
}

export default index;